@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
.menu {
    background-color: black;
    box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    position: fixed;
}

.menu__right {
    display: flex;
    align-items: center;
    height: 100%;
}

.menu__list {
    list-style-type: none;
    padding: 0;
    height: 100%;
    margin: 0;
    margin-right: 40px;
}

.menu__list-item {
    display: inline-block;
    height: 100%;
    margin-right: 20px;
}

.menu__list-item:last-child {
    margin-right: 0;
}

.menu__link {
    color: white;
    display: inline-block;
    height: 100%;
    font-size: 16px;
    text-decoration: none;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding: 0 3px;
}

.menu__link:hover {
    color: gray;
    border-top: 4px solid gray;
    font-weight: 700;
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.menu__logo {
    text-indent: -9999999px;
    display: inline-block;
    height: 100%;
    width: 92px;
    background-size: cover 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin: 0;
}
.btn {
    cursor: pointer;
    height: 42px;
    border-radius: 5px;
    border: 1px solid black;
    background: none;
    margin-top: 20px;
    background: white;
    color: black;
    font-weight: bold;
    font-size: 1rem;
    text-decoration: none;
    padding: 10px;
    display: 1;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    width: 280px;
    text-transform: uppercase;
  }
  
  .btn:hover {
    border: 1px solid white;
    background: black;
    color: white;
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }

#Lista{
    text-align: left;
    display: flex;
    align-items: left;
    justify-content: left;
    background-color: white;
    background-position: left top;
    flex-direction: column;
    padding: 0px;
    font-size: 12px;
}

#Topo{
    text-align: left;
    display: flex;
    align-items: left;
    justify-content: left;
    background-color: white;
    background-position: left top;
    flex-direction: row;
    padding: 0px;
}



#Lista h1{
    margin-top: 5px;
    margin-left: 10px;
    font-size: 20px;
}

#Lista img_lista {
    border: 1px solid black;
    width: 30px;
    height: 30px;
}

#Lista img{
    width: 30px;
    height: 30px;
    object-fit: fill;
}

#Lista li{
    font-size: 20px;
    color: rgb(110, 107, 107);
    margin-left: 50px;
    padding: 5px 0px;
}




#List {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    background-position: left top;
    flex-direction: row;
    padding: 20px;
    flex-wrap: wrap;
}
#List h2{
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif ;
    font-style: normal;
}

.Nome{
    color: gray;
    font-style: italic;
}

perfilImg {
    border: 1px solid black;
    width: 400px;
    height: 400px;
}

img{
    width: 400px;
    height: 400px;
    object-fit: fill;
    border-radius: 200px;
}


#ListDir, #ListEsq {
    flex: 1 1;    
    margin-top: 60px;
}


#ListEsq ul{
    margin-bottom: 50px;
}

#ListEsq .item-1{
    font-size: 55px;
    margin-top: 20px;
    margin-bottom: 40px;
    text-decoration: underline;
}


#skills {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: left top;
    flex-direction: column;
    padding: 20px;
    color: white;
}

#skills h1{
    margin-top: 50px;
    text-transform: uppercase;
    font-size: 55px;
    text-decoration: underline;
    margin-bottom: 20px;
}

#skills .item-3{
    margin: 50px;
}

#skills .item-2{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-transform: uppercase;
    margin-bottom: 50px;
} 


#skills .item-4:hover {
    background: white;
    color: black;
}

#skills .item-4{
    padding: 10px;
    background-color: black;
    margin: 10px;
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}


#Portfolio {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    flex-direction: column;
    padding: 20px;
    flex-wrap: wrap;
    background-size: cover;
    background-repeat: no-repeat;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(34, 34, 59, 0.84)), to(rgba(34, 34, 59, 0.98))), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/work.jpg?raw=true");
    background: -webkit-linear-gradient(rgba(34, 34, 59, 0.84), rgba(44, 44, 99, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/work.jpg?raw=true");
    background: -webkit-linear-gradient(rgba(34, 34, 59, 0.84), rgba(34, 34, 59, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/work.jpg?raw=true");
    background: linear-gradient(rgba(34, 34, 59, 0.84), rgba(34, 34, 59, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/work.jpg?raw=true");
    background-position: center center;
    background-attachment: fixed;
}

#Portfolio h1{
    margin-top: 50px;
    text-transform: uppercase;
    font-size: 55px;
    text-decoration: underline;
}

#Portfolio .body{
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;
    margin-bottom: 50px;
}

#Portfolio .image {
    cursor: pointer;
    border: 1px solid black;
    margin-left: 20px;
    margin-bottom: 20px;
    margin-right: 20px;
    width: 250px;
    height: 200px;
    border-radius: 20px;
    
}

#Portfolio img{
    width: 250px;
    height: 200px;
    object-fit: fill;
    border-radius: 20px;
}

#Portfolio .image:hover{
    box-shadow: 9px 7px 5px rgba(17, 17, 17, 0.77);
    -webkit-transition: all 1.0s ease-in-out 0s;
    transition: all 1.0s ease-in-out 0s;
}


#Info{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    background-position: left top;
    flex-direction: column;
    padding: 20px;
    flex-wrap: wrap;
    color: white;
}

#Info .body{
    margin-top: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 70px;
    margin-right: 70px;
}

#Info h1{
    text-transform: uppercase;
    font-size: 55px;
    text-decoration: underline;
    margin-top: 55px;
}



#Info h2{
    text-transform: uppercase;
    font-size: 35px;
    text-decoration: underline;
    margin-bottom: 25px;
}

#Info .work h2{
    margin-left: 35px;
}

#Info .bio h2{
    margin-right: 35px;
}
#Info .bio p{
    font-size: 25px;
    margin-bottom: 100px;
    margin-right: 35px;
}

#Info .work p{
    font-size: 25px;
    margin-bottom: 100px;
    margin-left: 35px;
}

@media only screen and (max-width: 768px) {
    #Info .body{ 
        display: flex;
        flex-wrap: wrap;
        margin-left: 0px;
        margin-right: 0px;
    }
    #Info .work p{
        margin-left: 0px;
        margin-right: 0px;
    }
    #Info .work h2{
        margin-left: 0px;
        margin-right: 0px;
    }

    #Info .bio p{
        margin-left: 0px;
        margin-right: 0px;
    }
    #Info .bio h2{
        margin-left: 0px;
        margin-right: 0px;
    }
}

#Info img{
    height: 100px;
    width: 100px;
    margin-bottom: 50px;
}


#Blog {
    display: flex;
    align-content: center;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-position: left top;
    background-color: white;
    flex-wrap: wrap;
    flex-direction: column;
    color: black;
}

#Blog h1{
    font-size: 55px;
    margin-top: 50px;
    text-decoration: underline;
}

#Blog .post{
    margin-top: 30px;
    margin-bottom: 50px;
    display: flex;
    align-content: center;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
}

#Blog .item{
    color: white;
    background-color: black;
    margin: 20px;
    width: 300px;
    height: 600px;
}

#Blog h2{
    margin-top: 20px;
    text-decoration: underline;
    margin-bottom: 20px;
    font-size: 30px;
}

#Blog .img-blog {
    border: 1px solid white;
    width: 100%;
    height: 200px;
    margin-bottom: 10px;
}

#Blog img{
    width: 100%;
    height: 200px;
    object-fit: fill;
}


#Contato {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    flex-wrap: wrap;
    background: black;
    color: white;
    background-size: cover;
    background-repeat: no-repeat;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(34, 34, 59, 0.84)), to(rgba(34, 34, 59, 0.98))), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/contato.jpg?raw=true");
    background: -webkit-linear-gradient(rgba(34, 34, 59, 0.84), rgba(44, 44, 99, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/contato.jpg?raw=true");
    background: -webkit-linear-gradient(rgba(34, 34, 59, 0.84), rgba(34, 34, 59, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/contato.jpg?raw=true");
    background: linear-gradient(rgba(34, 34, 59, 0.84), rgba(34, 34, 59, 0.98)), url("https://github.com/antonioducs/proweb/blob/master/img/portifolio/contato.jpg?raw=true");
    background-position: center center;
    background-attachment: fixed;
}

#Contato h1{
    margin-top: 50px;
    font-size: 55px;
    text-decoration: underline;
}

#Contato h2{
    margin-top: 30px;
}

#Contato p{
    margin-bottom: 10px;
}

#Contato #assunto, #Contato #nome, #Contato #email{
    font-size: 20px;
    margin-bottom: 20px;
    width: 80%;
    height: 30px;
    border-radius: 20px;
    padding: 10px;
}
#Contato textarea{
    padding: 10px;
    margin-top: 10px;
    width: 80%;
    border-radius: 20px;
    height: 200px;
}

#Contato Button{
    margin-bottom: 50px;
}

#Contato .item{
    margin-bottom: 30px;
    margin-left: 20px;
    margin-right: 20px;
}

#Contato img{
    height: 100px;
    width: 100px;
    border-radius: 0px;
    margin-bottom: 50px;
}

